import React, {useEffect, useState} from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";

import axios from "axios";
import ReactEcharts from 'echarts-for-react';
import PropTypes from "prop-types";

const styles = {
  typo: {
    paddingLeft: "10%",
    marginBottom: "40px",
    position: "relative"
  },
  note: {
    fontFamily: '"Roboto", "Helvetica", "Arial", sans-serif',
    bottom: "10px",
    color: "#000000",
    display: "block",
    fontWeight: "400",
    fontSize: "15px",
    lineHeight: "13px",
    left: "0%",
    marginLeft: "20px",
    position: "absolute",
    top: "50%", 
    width: "100px"
  },
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0"
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none"
  }
};

const useStyles = makeStyles(styles);

export default function ChartG2({kind}) {
  const classes = useStyles();

  const url2 = "https://p2yn7tplgd.execute-api.ap-northeast-2.amazonaws.com/v2/query-to-api2" + "?kind=" + kind;

  const [data_2, setData_2] = useState([]);


  useEffect(() => {
    axios
    .get(url2)
    .then(result => setData_2(result.data));
  }, []);

  var data2 = data_2[0]
  var keylist = [];
  for(var i in data2){
    keylist.push(i)
  }


  var j = 0;
  var arrApp = '';
  var arrFinal = [];
  var series_option = [];
  while(j<=10){
      arrApp = (new Function ("return"+"arrApp_"+ j));
      var arrApp = Array.from(data_2, v=>v[keylist[j]])
      arrFinal.push(arrApp)
      j = j+1;
  }

  var k = 1;
    while(k<=(arrFinal.length-1)){
        var series1 = {
            name : keylist[k],
            type : 'bar',
            stack : 'total',
            label : {
                show : false
            },
            emphasis : {
                focus : 'series'
            },
            data : arrFinal[k]
        } 
        series_option.push(series1)
        k=k+1;
    }


  return (
    <ReactEcharts theme = {"dark"} option = {{
      title : {
          text : '누적 구매횟수와 매출합'
      },
      tooltip : {
          trigger : 'axis',
          axisPointer : {
              type : 'cross',
              crossStyle : {
                  color : '#999'
              }
          }
      },
      toolbox: {
        feature: {
          dataView: {show: true, readOnly: false},
          magicType: {show: true, type: ['line', 'bar']},
          restore: {show: false},
          saveAsImage: {show: true}
        }
      },
      grid: {
        top: '20%',
        left: '5%',
        right: '5%',
        bottom: '10%',
        containLabel: true,
      },
      legend : {
          data : keylist
      },
      xAxis : {
          data : arrFinal[0],
          name : '년-월',
          nameLocation: 'middle',
          nameGap: 30,
          axisPointer : {
                  type : 'shadow'
                  }
          },
      yAxis : [{
          type : 'value',
          name : '구매금액합계',
          nameLocation: 'middle',
          nameGap: 70,
          axisLabel : {
                  formatter:'{value}'
                  }
      }],
      series : series_option
    }} />
  );
}

ChartG2.propTypes = {
  kind : PropTypes.string
}