import React, {useEffect, useState} from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";


import axios from "axios";
import ReactEcharts from 'echarts-for-react';
import PropTypes from "prop-types";



const styles = {
  typo: {
    paddingLeft: "13%",
    marginBottom: "40px",
    position: "relative"
  },
  note: {
    fontFamily: '"Roboto", "Helvetica", "Arial", sans-serif',
    bottom: "10px",
    color: "#000000",
    display: "block",
    fontWeight: "400",
    fontSize: "15px",
    lineHeight: "13px",
    left: "0%",
    marginLeft: "20px",
    position: "absolute",
    top: "0%", 
    width: "120px"
  },
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0"
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none"
  }
};

const useStyles = makeStyles(styles);


export default function CohortChart({kind}) {
  const classes = useStyles();

  const url = "https://y0xy30q5e7.execute-api.ap-northeast-2.amazonaws.com/v4/query_to_api4"+ "?kind=" + kind;


  const [data, setData] = useState([]);

    useEffect(() => {
        axios
        .get(url)
        .then(result => setData(result.data));
    }, []);

    // console.log(data)
    var data2 = data[0]
    var keylist = [];

    for(var i in data2){
        keylist.push(i)
    }
    // console.log(keylist)

    var j = 0;
    var arrApp = '';
    var arrFinal = [];
    while(j<=16){
        arrApp = (new Function ("return"+"arrApp_"+ j));

        var arrApp = Array.from(data, v=>v[keylist[j]])
        arrFinal.push(arrApp)
        j = j+1;
        
    }
    // console.log(arrFinal)

    var data2 = [];
    var k=0;
    for(var step=1; step<17; step++){
        for(var n in arrFinal[step]){
            var zero = [k, arrFinal[step].length - Number(n)-1 ,arrFinal[step][n]]
            data2.push(zero)
        }
        k+=1
    }
  

  return (
          <ReactEcharts theme = {"dark"} option = {{
            title : {
                text : '코호트 재구매율 (%)'
            },
            tooltip : {
                position :'top'
            },
            animation : false,
            toolbox: {
	            feature: {
		            dataView: {show: false, readOnly: false},
		            magicType: {show: false, type: ['line', 'bar']},
		            restore: {show: false},
		            saveAsImage: {show: true}
	            }
            },
            grid: {
	            height: '75%',
	            y: '10%',
            },
            xAxis : {
                type : 'category',
                name : '차이',
                nameLocation: 'middle',
                nameGap: 24,
                data : keylist.slice(1),
                splitArea : {
                        show : true
                        }
                },
            yAxis : {
                type : 'category',
                name : '첫구매일자',
                nameLocation: 'middle',
                nameGap: 70,
                data : arrFinal[0].reverse(),
                splitArea : {
                    show: true
                        }
            },
            visualMap : {
                min:0,
                max:100,
                inRange : {   
                    color: ['#3873f2', '#f7f563' ] //From smaller to bigger value ->
                },
                calculable : true,
                orient : 'vertical',
                left :'90%',
                bottom : '35%',
                top : 'middle'
            },
            series : [{
                name : 'Cohort',
                type : 'heatmap',
                data : data2,
                label : {
                    normal :{
                        show : true
                    } 
            },
            itemStyle : {
                emphasis: {
                    shadowBlur: 10,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                    }
                }
            }]
        }} />
  );
}

CohortChart.propTypes = {
  kind : PropTypes.string
}