import React, {useEffect, useState} from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";

import axios from "axios";
import ReactEcharts from 'echarts-for-react';
import PropTypes from "prop-types";

const styles = {
  typo: {
    paddingLeft: "10%",
    marginBottom: "40px",
    position: "relative"
  },
  note: {
    fontFamily: '"Roboto", "Helvetica", "Arial", sans-serif',
    bottom: "10px",
    color: "#000000",
    display: "block",
    fontWeight: "400",
    fontSize: "15px",
    lineHeight: "13px",
    left: "0%",
    marginLeft: "20px",
    position: "absolute",
    top: "50%", 
    width: "100px"
  },
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0"
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none"
  }
};

const useStyles = makeStyles(styles);



export default function ChartG3({kind}) {
  const classes = useStyles();
  const url3 = "https://m8y3uw80qf.execute-api.ap-northeast-2.amazonaws.com/v3/query_to_api3" + "?kind=" + kind;

  const [data_3, setData_3] = useState([]);


  useEffect(() => {
    axios
    .get(url3)
    .then(result => setData_3(result.data));
  }, []);

  var data3 = data_3[0]
  var keylist2 = [];

  for(var i in data3){
      keylist2.push(i)
  }


  var jj = 0;
  var arrRank = '';
  var arrFinal2 = [];
  var series_option2 = [];
  while(jj<=10){
     arrRank = (new Function ("return"+"arrRank_"+ jj));
      var arrRank = Array.from(data_3, v=>v[keylist2[jj]])
      arrFinal2.push(arrRank)
      jj = jj+1;
  }

  var kk = 1;
  while(kk<=(arrFinal2.length-1)){
      var series2 = {
          name : keylist2[kk],
          type : 'bar',
          stack : 'total',
          label : {
              show : false
          },
          emphasis : {
              focus : 'series'
          },
          data : arrFinal2[kk]
      } 
      series_option2.push(series2)
      kk=kk+1;
  }


  return (
    <ReactEcharts theme = {"dark"} option = {{
      title : {
          text : '랭크당 월별 금액'
      },
      tooltip : {
          trigger : 'axis',
          axisPointer : {
              type : 'cross',
              crossStyle : {
                  color : '#999'
              }
          }
      },
      toolbox: {
        feature: {
          dataView: {show: true, readOnly: false},
          magicType: {show: true, type: ['line', 'bar']},
          restore: {show: false},
          saveAsImage: {show: true}
        }
      },
      grid: {
        top: '20%',
        left: '5%',
        right: '5%',
        bottom: '10%',
        containLabel: true,
      },
      legend : {
          data : keylist2
      },
      xAxis : {
          data : arrFinal2[0],
          name : '년-월',
          nameLocation: 'middle',
          nameGap: 30,
          axisPointer : {
                  type : 'shadow'
                  }
          },
      yAxis : [{
          type : 'value',
          name : '구매금액합계',
          nameLocation: 'middle',
          nameGap: 70,
          axisLabel : {
                  formatter:'{value}'
                  }
      }],
      series : series_option2
    }} />
  );
}

ChartG3.propTypes = {
  kind : PropTypes.string
}