const useStyles = makeStyles(styles);



export default function LTVChart({kind}) {
  const classes = useStyles();

  const url = "https://0qwhxrc3xj.execute-api.ap-northeast-2.amazonaws.com/ltv/ltv"+ "?kind=" + kind;

  const [data, setData] = useState([]);

    useEffect(() => {
        axios
        .get(url)
        .then(result => setData(result.data));
    }, []);

    // console.log(data)
    var data2 = data[0]
    var keylist = [];

    for(var i in data2){
        keylist.push(i)
    }
    // console.log(keylist.length)

    var j = 0;
    var arrApp = '';
    var arrFinal = [];
    while(j<=16){
        arrApp = (new Function ("return"+"arrApp_"+ j));

        var arrApp = Array.from(data, v=>v[keylist[j]])
        arrFinal.push(arrApp)
        j = j+1;
        
    }
    // console.log(arrFinal)

    var data2 = [];
    var k=0;
    for(var step=1; step<17; step++){
        for(var n in arrFinal[step]){
            var zero = [k, arrFinal[step].length - Number(n)-1 ,arrFinal[step][n]]
            data2.push(zero)
        }
        k+=1
    }
    // console.log(data2)

    // var n =data2[225].slice();// data2[225]
    // console.log("========>",n,n[0], typeof(n) )
    // var mya = [1,2,3]
    // console.log("========>",mya,mya[0], typeof(mya) )
    // for( n of a )
    //     console.log("========>",n )
    // console.log(data2[225])
    // console.log(data2[61])
    // console.log(data2[156])
    if (kind == 'Global'){
        var maxnum = 11055.02;
    }
    else if (kind == 'Brazil'){
        var maxnum = 189.07
    }
    else {var maxnum = 5098.47}
    

  return (
          <ReactEcharts theme = {"dark"} option = {{
            title : {
                text : '코호트 LTV ($)'
            },
            tooltip : {
                position :'top'
            },
            animation : false,
            toolbox: {
	            feature: {
		            dataView: {show: false, readOnly: false},
		            magicType: {show: false, type: ['line', 'bar']},
		            restore: {show: false},
		            saveAsImage: {show: true}
	            }
            },
            grid: {
	            height: '75%',
	            y: '10%',
            },
            xAxis : {
                type : 'category',
                name : '차이',
                nameLocation: 'middle',
                nameGap: 24,
                data : keylist.slice(1),
                splitArea : {
                        show : true
                        }
                },
            yAxis : {
                type : 'category',
                name : '첫구매일자',
                nameLocation: 'middle',
                nameGap: 70,
                data : arrFinal[0].reverse(),
                splitArea : {
                    show: true
                        }
            },
            visualMap : {
                min: 0,
                max: maxnum,
                inRange : {   
                    color: ['#53fc86', '#fab846' ] //From smaller to bigger value ->
                },
                calculable : true,
                orient : 'vertical',
                left :'90%',
                bottom : '35%',
                top : 'middle'
            },
            series : [{
                name : 'Cohort',
                type : 'heatmap',
                data : data2,
                label : {
                    normal :{
                        show : true
                    } 
            },
            itemStyle : {
                emphasis: {
                    shadowBlur: 10,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                    }
                }
            }]
        }} />
  );
}
