import React, {useEffect, useState} from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";

import axios from "axios";
import ReactEcharts from 'echarts-for-react';
import PropTypes from "prop-types";

const styles = {
  typo: {
    paddingLeft: "10%",
    marginBottom: "40px",
    position: "relative"
  },
  note: {
    fontFamily: '"Roboto", "Helvetica", "Arial", sans-serif',
    bottom: "10px",
    color: "#000000",
    display: "block",
    fontWeight: "400",
    fontSize: "15px",
    lineHeight: "13px",
    left: "0%",
    marginLeft: "20px",
    position: "absolute",
    top: "50%", 
    width: "100px"
  },
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0"
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none"
  }
};

const useStyles = makeStyles(styles);



export default function ChartG1({kind}) {
  const classes = useStyles();

  // const kind = props;

  const url1 = "https://7i0mi3qtaj.execute-api.ap-northeast-2.amazonaws.com/v1/query-to-api1" +"?kind="+ kind;

  const [data_1, setData_1] = useState([]);


  useEffect(() => {
        axios
        .get(url1)
        .then(result => setData_1(result.data));
  }, []);
  var arrDate = Array.from( data_1, v=> v.orderdate);
  var arrSum = Array.from(data_1, v=> v.sum_s);
  var arrCount = Array.from(data_1, v=> v.count_s) 


  return (
          <ReactEcharts theme={"dark"} option = {{
            title: {text:'월별 구매량과 구매건수'},
            tooltip : {
                trigger : 'axis',
                axisPointer : {
                type : 'cross',
                crossStyle : {
                  color : '#999'
                  }
                }},
            toolbox: {feature: {
		            dataView: {show: true, readOnly: false},
		            magicType: {show: true, type: ['line', 'bar']},
		            restore: {show: false},
		            saveAsImage: {show: true}
	            }
            },
            grid: {
	            top: '20%',
	            left: '5%',
	            right: '5%',
	            bottom: '10%',
	            containLabel: true,
            },
            legend: {
                data : ['구매금액합계(월별)','구매건수합계(월별)']
            },
            xAxis: {
                data : arrDate,
                name : '년-월',
                nameLocation: 'middle',
                nameGap: 30,
                axisPointer : {
                        type : 'shadow'
                        }
            },
            yAxis: [{
                type : 'value',
                name : '구매금액합계(월별)',
                nameLocation: 'middle',
                nameGap: 70,
                axisLabel : {
                        formatter:'{value}'
                        }
            },
            {
                type : 'value',
                name : '구매건수합계(월별)',
                nameLocation: 'middle',
                nameGap: 60,
                offset : 0,
                axisLabel : {
                        formatter : '{value}'
                        }
            }],
            series: [{
                name:'구매금액합계(월별)',
                type: 'bar',
                data: arrSum
            },
            {
                name:'구매건수합계(월별)',
                type: 'line',
                yAxisIndex : 1,
                itemStyle: {
			            normal: {
				            color: '#fbff14'
			                    }
                },
                lineStyle : {
                    color : '#fbff14',
                    width : 5 
                },
                symbolSize: 7,
                data: arrCount
            }]
          }} />
  );
}

ChartG1.propTypes = {
  kind : PropTypes.string
}